.modal {
  position: fixed;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  z-index: 100;
  min-width: 60vw;
  max-width: 80rem;
  overflow: hidden;
  max-height: 60vh;
  /* height: 60vh; */
  display: flex;
  flex-direction: column;
}
.content {
  /* height: calc(100% - 3.2rem); */
  max-height: calc(60vh - 5.5rem);
  display: flex;
  flex-direction: column;
  flex: auto;
}

.expand.modal {
  border-radius: 0;
  width: 100vw;
  height: 100vh;
  transform: translate(0, 0);
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
}
.header {
  padding: 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.header > *:last-child {
  text-align: end;
}

.title {
  margin: 0;
  color: #555;
  flex: 1;
  font-weight: 500;
  font-size: 1.6rem;
}
.expand > .header {
  padding: 1rem 2rem;
}

.expand > .header > .title {
  font-size: 2.4rem;
}

.cancel {
  font-size: 2.4rem;
  cursor: pointer;
}

.expand > .content {
  flex: auto;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

@media (min-width: 460px) {
  .modal {
    min-width: fit-content;
    width: calc(100vw - 4rem);
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    /* height: fit-content; */
    border-radius: 10px;
  }
}

@media (min-width: 700px) {
  .expand.modal {
    min-width: fit-content;
    width: calc(100vw - 4rem);
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    height: fit-content;
    border-radius: 10px;
  }
}
